/************** CUSTOMERS SCROLL SECTION  ****************/
/***************************************************/
::-webkit-scrollbar {    
  width: 5px;
}
/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #ffffff; 
  border-radius: 10px;
}   
/* Handle */
::-webkit-scrollbar-thumb {    
  background: #0080FF; 
  border-radius: 10px;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #ff0000b7; 
}

