/**************ALERT*****************/

.alert{
  transform: translate(0%, 1000%);
  transition: all 2s linear;
  width:100%;
  position:fixed;
  background-color: #002a68;
  left: 0;
  z-index: 1000;
  border-radius: 0;
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}

.col_alert{
  height:300px;
  width:250px;
  background-color: #fff;
  border-radius: 10px;
  margin-left: 20px;
  margin-top: 20px;
}

.col_alert_maps{
  width: 100%;
  height: 240px;
  border-radius: 10px;
}

.col_alert_title{
  text-transform: uppercase;
  font-size: smaller;
  text-align: center;
  font-weight: bold;
  margin-top: 10px;
}



.scrool{
  overflow-y: scroll !important;
  overflow-x: hidden !important;
  height:550px;
  width:100%;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
  div{
    margin: auto;
    width: 70%;
    background-color: #ddd;
    padding: 2em;
  }

}

.alert_close{
  margin-left: 90%;
}

@media screen and (max-width: 700px) {
  
.scrool{
  div{
    width: 100%;
    padding: 0;
    margin-top: 1em;
  }
  .alert{
    padding: 0;
  }

  .alert_close{
    margin-left: 0!important;
  }
}
}


