.dropDownMenuContainer{
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
    background-color: white;
    transition: height .5s;
    overflow: auto;
    z-index: 10;
}

.toggleDropDownMenu{
    padding: 0!important;
    height: 100vh;
}

.searchAndClose{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 2em;
    height: 20%;
    border-bottom: 1px solid;
}

.closeBtn{
    color: #0080FF;
    cursor: pointer;
    font-weight: bolder;
    font-size: 40px;
}

.formContainer{
    display: flex;
    gap: 1em;
    align-items: center;
}

@media screen and (max-width: 1280px) {
    .toggleDropDownMenu{
        margin: 0px 0;
        padding: 0 0 200px 0;
    }
}